.container__search {
  padding-top: 60px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    padding-left: 75px;
    padding-right: 75px;
  }

  @media screen and (min-width: 1024px) {
    padding-left: 71px;
    padding-right: 71px;
  }

  @media screen and (min-width: 320px) {
    width: 320px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
  }
  @media screen and (min-width: 1024px) {
    width: 1024px;
  }
}
.is-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.gallery-list {
  width: 100%;
  height: 100%;
  margin-bottom: 50px;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 50px;
    padding: 0;
  }

  @media screen and (min-width: 1024px) {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 50px;
    padding: 0;
  }
}

.plug {
  @media screen and(max-width: 1024px) {
    display: none;
  }
}

.gallery-item {
  box-shadow: 2px 1px 4px 2px #00000033;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  transition: 500ms cubic-bezier(0.4, 0, 0.2, 1);
  animation: zoomIn;
  animation-duration: 2s;
  animation-delay: 0.9ss;
  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &:hover {
    transform: scale(1.1);
  }

  @media screen and (min-width: 768px) {
    box-shadow: 2px 1px 4px 2px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    flex-basis: auto;
    animation: zoomIn;
    animation-duration: 2s;
    animation-delay: 0.9ss;

    &:nth-child(2n + 1) {
      margin-right: 30px;
    }

    &:nth-child(-n + 18) {
      margin-bottom: 30px;
    }

    &:hover {
      transform: scale(1.1);
    }
  }

  @media screen and (min-width: 1024px) {
    // flex-basis: calc(100% / 3 - 30px);
    box-shadow: 2px 1px 4px 2px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    flex-basis: auto;
    margin-right: 30px;
    margin-bottom: 30px;
    animation: zoomIn;
    animation-duration: 2s;
    animation-delay: 0.9ss;

    &:hover {
      transform: scale(1.1);
    }
  }
}

.gallery-item:nth-child(3n) {
  @media screen and (min-width: 1024px) {
    margin-right: 0;
  }
}

.gallery-item:nth-last-child(-n + 3) {
  @media screen and (min-width: 1024px) {
    margin-bottom: 0;
  }
}

.comma:last-child {
  display: none;
}

