.plug {
  width: 274px;
  height: 470px;
}

.plug-container {
  display: block;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  align-items: center;
  margin: 0;

  border: solid 5px #ff6b08;
}

.film-img {
  width: 280px;
  height: 402px;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    width: 294px;
    height: 398px;
    cursor: pointer;
  }

  @media screen and (min-width: 1024px) {
    width: 274px;
    height: 398px;
    cursor: pointer;
  }
}

.film-info {
  
  flex-wrap: wrap;
  margin-top: 10px;
  margin-bottom: 20px;
  padding-left: 5px;
  // width: 280px;

  @media screen and (min-width: 768px) {
    width: 294px;
    margin-bottom: 30px;
    padding-left: 5px;
  }

  @media screen and (min-width: 1024px) {
    width: 274px;
    margin-bottom: 30px;
    padding-left: 5px;
  }
}

.film-title-dark {
  flex-wrap: wrap;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
}

.film-title {
  flex-wrap: wrap;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
}

.film-text {
  flex-wrap: wrap;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  // word-wrap: break-word;
  color: #ff6b01;
}

.rait {
  width: 36px;
  height: 16px;
  padding: 1px 10px;
  margin-left: 8px;
  border-radius: 5px;
  background: #ff6b01;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff;
}

